import examplePic from '../example-pic/example-pic.vue'
export default {
  components: {
    
    examplePic
  },
  props: {
    showModel: {
      type: String,
      default: ''
    },
    countHeight: {
      type: String,
      default: ''
    },
    tabCurrentIndex: {
      type: Number,
      default: 0
    },
    navList: {
      type: Array,
      default: () => []
    },
    MaterialsInfo: {
      type: Array,
      default: () => []
    },
    designImgList: {
      type: Array,
      default: () => []
    },
    dynamicList: {
      type: Array,
      default: () => []
    },
    quoteContent: {
      type: String,
      default: ''
    },
    isComplete: {
      type: Boolean,
      default: false
    },
   
  },
  data() {
    return {
      widHeight:'',
      heights:[],
      // navListCopy:[],
      
    }
  },
  
  
  mounted() {
    console.log(this.isComplete)
    console.log(this.dynamicList)
    // if(this.isComplete){
    //   this.navListCopy =  this.navList.filter(item => item.state !== 0)
    // }else{
    //   this.navListCopy =  this.navList.filter(item => item.state !== 3)
    // }
  },
  methods: {
    loadData(e) {
      this.$emit('handleLoadData', e.target.current)
    },
    
    
    // swiper 切换
    changeTab(e) {
      this.$emit('handleSwiper', e.target.current)
    },
    // 顶部tab点击切换
    tabClick(index) {		
      this.$emit('handleTabClick', index)
    },
		cantchMove(){
			return false
		},
    handleDynamicFunc(){
      
      this.$emit('handleDynamicOne')
      
     
    }
  }
}
